import React, { useEffect, useState } from "react";
import {
	withScriptjs,
	withGoogleMap,
	GoogleMap,
	Marker,
} from "react-google-maps";

const WrappedMap = withScriptjs(withGoogleMap(MapComponent));

function MapComponent(props: any) {
	const pos = props.position;
	return (
		<GoogleMap
			defaultZoom={8}
			defaultCenter={{ lat: pos.latitude, lng: pos.longitude }}
		>
			<Marker position={{ lat: pos.latitude, lng: pos.longitude }} />
		</GoogleMap>
	);
}

export default function MapWrapper(props: any) {
	console.log(props.mapStyle);
	return (
		<WrappedMap
			position={props.position}
			containerElement={<div style={props.mapStyle}></div>}
			mapElement={<div style={{ height: `100%` }} />}
			googleMapURL={
				"https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places"
			}
			loadingElement={<div style={{ height: `100%` }} />}
		></WrappedMap>
	);
}
